import 'object/object.property';
import 'element/element.property';

// facebook custom share element
export default class FBShare {
    constructor (options = {}) {
        this._options = Object.assign({
            method: 'share',
            display: 'popup'
        }, options);

        this._FBShareEle = [];

        if (options.ele && options.ele !== '') this.addShare(options.ele);
    }

    addShare (ele, options = {}) {
        let _options = Object.assign(this._options, options),
            _ele = document.querySelectorAll(ele);

        for (let i = 0; i < _ele.length; i++) {
            _ele[i].FBShareOptions = Object.assign({}, _options);

            _ele[i].getDataset();
            
            if (_ele[i].dataset.href) _ele[i].FBShareOptions.href = _ele[i].dataset.href;

            _ele[i].onclick = function (event) {
                console.log(_ele[i].FBShareOptions);
                if (typeof _ele[i].FBShareOptions.href !== 'string' || _ele[i].FBShareOptions.href === '')
                    _ele[i].FBShareOptions.href = window.location.href;
                console.log(_ele[i].FBShareOptions);
                FB.ui(_ele[i].FBShareOptions, function (response) {
                    if (typeof _ele[i].FBShareOptions.callback === 'function')
                        _ele[i].FBShareOptions.callback(response, this);
                });
            };

            this._FBShareEle.push(_ele[i]);
        }
    }

    get shareEle () { return this._FBShareEle; }
}
